import React from "react";
import PropTypes from "prop-types";

const Tile =(props)=>{
    return (
    <div className={"tile " + props.customClass} data-aos={props.transition}>
        <div className="tile__image">
            <picture>
                <img src={props.img} alt={props.alt} />
            </picture>
        </div>
        <div className="tile__content">
            <h3>
                {props.title}
            </h3>
           {props.children}
        </div>
    </div>)

}
Tile.defaultProps = {
    img: "logo.svg",
    alt:'alt',
    title:"Title",
    transition:"fade-up",
    customClass:'',
    children: []
  };
  
Tile.propTypes = {
    img: PropTypes.string,
    title: PropTypes.string,
};
export default Tile